import UserInputArea from "./userinput/UserInput";
import NameScreen from "./namescreen/NameScreen";
import AnswersTab from "./answerstab/AnswersTab";
import React from "react";
import "./app.css";

export const blurAmount = "5px";
export const maxNameLength = "20";
function App() {
  const [username, setUsername] = React.useState(() => "");

  const [errorMessage, setErrorMessage] = React.useState(() => "");

  const [prompt, setPrompt] = React.useState(
    "It's your turn to ask a question"
  );

  const [promptType, setPromptType] = React.useState(() => "a");

  const [blur, setBlur] = React.useState(true);

  const [qid, setQid] = React.useState(null);

  const [answers, setAnswers] = React.useState(() => {});

  const [reloadButtonClass, setReloadButtonClass] =
    React.useState("reload-button");

  const handleServerRes = (res) => {
    if (res.ok) {
      res.json().then((res) => {
        if (res.promptType === "a") {
          res.sensitive ? setBlur(true) : setBlur(false);
          setPrompt(`"${res.prompt}"`);
          setPromptType("a");
          setQid(res.qid);
        } else if (res.promptType === "q") {
          setBlur(false);
          setPrompt(res.prompt);
          setPromptType("q");
          setQid(res.qid);
        }
      });
    }
  };

  const fetchInitialPrompt = () => {
    fetch("/api/initialprompt")
      .then((resp) => (resp.ok ? resp.json() : new Error(resp.statusText)))
      .then((data) => {
        setPromptType(data.promptType);

        setQid(data.qid);

        data.promptType === "q"
          ? setPrompt(data.prompt)
          : setPrompt(`"${data.prompt}"`);

        if (!data.prompt) setPrompt("Failed to fetch data from server");

        !data.sensitive && username ? setBlur(false) : setBlur(true);
      })
      .catch((err) => console.error(err));
  };

  React.useEffect(() => {
    const handleInteract = (se) => {
      if (document.activeElement.id !== "input-bar" || !username)
        document.querySelector(".input-bar").focus();
    };

    document.addEventListener("keydown", handleInteract);
    document.addEventListener("click", handleInteract);

    fetch("/api/name")
      .then((resp) => (resp.ok ? resp.json() : new Error(resp.statusText)))
      .then((data) => {
        setUsername(data.username);

        fetchInitialPrompt();
      })
      .catch((err) => console.error(err));

    fetchInitialPrompt();

    fetch("/api/answers")
      .then((resp) =>
        resp.ok
          ? resp.json().then((data) => setAnswers(data))
          : console.error(`QnAPP: Failed to fetch answers ${resp.error()}`)
      )
      .catch(() => console.error("QnApp: Failed to fetch answers"));
  }, []);

  const handleAnswersReload = (se) => {
    setReloadButtonClass("reload-button disabled");
    setTimeout(() => {
      setReloadButtonClass("reload-button");
    }, 3000);

    fetch("/api/answers")
      .then((resp) => resp.json().then((data) => setAnswers(data)))
      .catch(() => console.log("QnApp: Failed to fetch answers"));
  };

  const handleNameSubmit = (se) => {
    se.preventDefault();
    const nameInput = document.querySelector(".input-bar").value.trim();

    if (nameInput.length > 0 && !nameInput.match(/[\p{P}\p{S}]/u)) {
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ username: nameInput }),
      };

      fetch("api/name", requestOptions)
        .then((response) => {
          if (response.ok) {
            setUsername(nameInput);
            fetchInitialPrompt();
          } else
            setErrorMessage(
              "Something went wrong trying to connect... Please reload"
            );
        })
        .catch(() => setErrorMessage("Could not connect to server"));
    } else {
      setErrorMessage("Name cannot contain special characters");
    }

    document.querySelector(".input-bar").value = "";
  };

  return (
    <div className="App">
      {!username && (
        <NameScreen
          handleNameSubmit={handleNameSubmit}
          errorMessage={errorMessage}
        />
      )}

      {username && <div className="name-display">{username}</div>}
      <div
        style={{
          filter: `blur(${blur ? blurAmount : 0})`,
        }}
        className="prompt-container"
        onMouseEnter={() => setBlur(false)}
      >
        {prompt}
      </div>
      <UserInputArea
        style={{ filter: `blur(${username ? 0 : blurAmount})` }}
        onServerResponse={handleServerRes}
        promptType={promptType}
        qid={qid}
        onResponseSent={() => {
          setPrompt("Sending response...");
        }}
      />
      <AnswersTab
        reloadButtonClass={reloadButtonClass}
        handleAnswersReload={handleAnswersReload}
        answers={answers}
      />
    </div>
  );
}

export default App;
