import React from "react";
import { maxNameLength } from "../App";

export default function NameScreen({
  handleNameSubmit = () => {},
  errorMessage = "hi",
}) {
  return (
    <div className="name-input-screen">
      <form className="name-input-container" onSubmit={handleNameSubmit}>
        <span className="name-input-prompt">What is your name?</span>
        <input
          className="input-bar"
          id="name-input"
          type="text"
          maxLength={maxNameLength}
        />
        <span className="name-input-error">{errorMessage}</span>
      </form>
    </div>
  );
}
