import React from "react";
import "./answerratebar.css";
import { IoMdThumbsUp, IoMdThumbsDown, IoMdFlag } from "react-icons/io";
import { rateActions } from "./UserInput";

const AnswerRateBar = ({ handleRate, rateState, buttonSize = 60 }) => {
  const isLiked = rateState.rated && rateState.liked;
  const isDisliked = rateState.rated && !rateState.liked;

  return (
    <div className="rate-bar">
      <button
        className="rate-option"
        id="like-option"
        onClick={() => handleRate({ type: rateActions.liked })}
        style={{ opacity: `${isLiked + 0.3}` }}
      >
        <IoMdThumbsUp
          className="option-icon"
          color="#27c840"
          size={buttonSize}
        />
      </button>

      <button
        className="rate-option"
        id="dislike-option"
        onClick={() => handleRate({ type: rateActions.disliked })}
        style={{ opacity: `${isDisliked + 0.3}` }}
      >
        <IoMdThumbsDown
          className="option-icon"
          color="#fe5f57"
          size={buttonSize}
        />
      </button>

      <button
        className="rate-option"
        id="report-option"
        onClick={() => handleRate({ type: rateActions.reported })}
        style={{ opacity: `${rateState.reported + 0.3}` }}
      >
        <IoMdFlag className="option-icon" color=" #ffbc30" size={buttonSize} />
      </button>
    </div>
  );
};

export default AnswerRateBar;
