import React from "react";
import { IoReloadOutline } from "react-icons/io5";

export default function AnswersTab({
  reloadButtonClass = "reload-button",
  handleAnswersReload = () => {
    return;
  },
  answers = {},
}) {
  return (
    <div
      className="answers-tab"
      onMouseLeave={(se) => se.target.scrollTo(0, 0)}
    >
      <IoReloadOutline
        className={reloadButtonClass}
        onClick={handleAnswersReload}
      />
      {answers &&
        Object.keys(answers)
          .reverse()
          .map((value) => {
            const currQuestion = answers[`${value}`];
            const likes = currQuestion.likes;
            const dislikes = currQuestion.dislikes;
            const totalResponses =
              likes + dislikes === 0 ? 2 : likes + dislikes;

            return (
              <div key={value} className="answer-element">
                <div className="answer-info">
                  <b>"{currQuestion.content}"</b>: {currQuestion.likes} likes/{" "}
                  {currQuestion.dislikes} dislikes
                </div>
                <div
                  className="dislikes-bar response-bar"
                  style={{
                    width: "90%",
                  }}
                ></div>
                <div
                  className="likes-bar response-bar"
                  style={{
                    width: `${
                      45 + ((likes - dislikes) / totalResponses) * 45
                    }%`,
                    zIndex: "20",
                  }}
                ></div>
                {currQuestion.answers.map((data, idx) => (
                  <div key={idx} className="question-response">
                    "{data}"
                  </div>
                ))}
              </div>
            );
          })}
    </div>
  );
}
