import React from "react";
import "./userinput.css";
import AnswerRateBar from "./AnswerRateBar";
import SearchButton from "./SearchButton";

export const maxQuestionLength = process.env.MAX_QUESTION_LENGTH || 60;
export const minBoxLength = process.env.MIN_BOX_LENGTH || 6;

const errorPersistDuration = 2000;
const errorFadeDuration = 500;
const errorShakeDuration = 300;

export const rateActions = {
  liked: "liked",
  disliked: "disliked",
  reported: "reported",
  reset: "reset",
};

const defaultRateState = {
  rated: false,
  liked: false,
  reported: false,
};

const rateReducer = (state, action) => {
  switch (action.type) {
    case rateActions.liked:
      return state.rated && state.liked
        ? { ...state, rated: false, liked: false }
        : { ...state, rated: true, liked: true };

    case rateActions.disliked:
      return state.rated && !state.liked
        ? { ...state, rated: false, liked: false }
        : { ...state, rated: true, liked: false };
    case rateActions.reported:
      return { ...state, reported: !state.reported };

    case rateActions.reset:
      return defaultRateState;
    default:
      throw new Error("Unexpected action type");
  }
};

const UserInput = ({
  onServerResponse = null,
  style = {},
  promptType = "a",
  qid = 0,
  onResponseSent = null,
}) => {
  const [inputText, setInputText] = React.useState("");

  const [showError, setShowError] = React.useState(false);

  const [errorMessage, setErrorMessage] = React.useState(() => "Error");

  const [isHidden, setIsHidden] = React.useState(true);

  const [goButtonClass, setGoButtonClass] = React.useState("go-button");

  const [timeoutId, setTimeoutId] = React.useState(null);

  const [hiddenTimeoutId, setHiddenTimeoutId] = React.useState(null);

  const [shakeTimeoutId, setShakeTimeoutId] = React.useState(null);

  const [rateState, rateDispatcher] = React.useReducer(
    rateReducer,
    defaultRateState
  );

  const [cursor, setCursor] = React.useState(() => 0);

  const handleUserType = (se) => {
    setCursor(se.target.selectionStart);
    setInputText(se.target.value);
  };

  const throwInputError = (message = "Error") => {
    setErrorMessage(message);

    setGoButtonClass("go-button error");
    setShowError(true);
    setIsHidden(false);

    if (timeoutId) clearTimeout(timeoutId);

    if (hiddenTimeoutId) clearTimeout(hiddenTimeoutId);

    if (!shakeTimeoutId) {
      setShakeTimeoutId(
        setTimeout(() => {
          setShakeTimeoutId(null);
          setGoButtonClass("go-button");
        }, errorShakeDuration + 5)
      );
    }
    setTimeoutId(
      setTimeout(() => {
        setTimeoutId(null);
        setShowError(false);
      }, errorPersistDuration)
    );

    setHiddenTimeoutId(
      setTimeout(() => {
        setIsHidden(true);
        setHiddenTimeoutId(null);
      }, errorPersistDuration + errorFadeDuration)
    );
  };

  /**
   * @param  {} se
   *
   * Takes in a submit event, shows an error message and start a timeout for when
   * the error message needs to be hidden again. In the case where a previous timeout
   * already exists, replace it with a new timeout to restart the timer
   */
  const handleUserSubmit = (se) => {
    se.preventDefault();

    if (inputText.trim().length < 2) {
      throwInputError("Please enter more than 1 character");
    } else {
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          user_response: inputText,
          qid: qid,
          rated: rateState.rated,
          liked: rateState.liked,
          reported: rateState.reported,
        }),
      };

      fetch("/api/questions", requestOptions)
        .then((data) => {
          if (onServerResponse) onServerResponse(data);
        })
        .catch((err) =>
          throwInputError(
            `Error happened: Could not connect to server!
            ${err}`
          )
        );

      if (onResponseSent) onResponseSent();
      rateDispatcher({ type: rateActions.reset });
    }
    setInputText("");
  };

  /**
   * @param  {} se
   * if the key is a navigation key i.e home, end, arrow keys, etc. they will be
   * prevented from the default behaviour. This is done since the placeholder css
   * cursor cannot move around and reflect the user's navigation
   */
  const handleInputKey = (se) => {
    if (se.keyCode >= 35 && se.keyCode <= 45) {
      se.preventDefault();
    }
  };

  return (
    <div className="user-interact-container" style={style}>
      <form id="user-input-container" onSubmit={handleUserSubmit}>
        <input
          type="text"
          id="input-bar"
          className="input-bar"
          onChange={handleUserType}
          value={inputText}
          maxLength={maxQuestionLength.toString()}
          onKeyDown={handleInputKey}
          onFocus={(se) => {
            se.target.selectionStart = cursor;
            se.target.selectionEnd = cursor;
          }}
        />
        <SearchButton mirrored={inputText} goButtonClass={goButtonClass} />
        <div
          className="error-message"
          hidden={isHidden}
          style={{ opacity: `${+showError}` }}
        >
          {errorMessage}
        </div>
        <span id="word-count">
          {inputText.length}/{maxQuestionLength}
        </span>
      </form>
      {promptType === "a" && (
        <AnswerRateBar handleRate={rateDispatcher} rateState={rateState} />
      )}
    </div>
  );
};
export default UserInput;
